<script>
import axios from "axios";
import DJ_C_MenuCard from "@/components/client/DJ_C_MenuCard.vue";
export default {
    data() {
        return {
            sname: null,
            shopInfo: [],
        };
    },
    watch: {
    '$route.query.spec': {
      immediate: true,
      // URL이 변경될 때마다 데이터를 가져오도록
      handler(newVal) {
        this.sname = newVal;
        this.fetchData(newVal); 
      },
    },
  },
    components: {
      DJ_C_MenuCard,
    },
  methods: {
    async fetchData(sname) {
        await axios.get('/shopnfo', {
          params: {
            spec: sname
          }
        }).then((res)=>{
          this.shopInfo = res.data;
          console.log(res.data)
        })
      .catch( (error)=>{
        console.error('Error while fetching data:', error);
      })
    },
  },
  mounted() {
    this.fetchData(this.$route.query.sname);
  },
};
</script>
  
  
